/* any element */
* {
	margin: 0;
	padding: 0;
	border: 0;
	outline: 0;
	box-sizing: border-box;
	list-style: none;
	text-decoration: none;
}

/* css variables that can be used anywhere in the project */
:root {
	/* --color-bg: #1f1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6); */

	--primary-hue: 270;
	--black-lightness: 15%;
	--dark-lightness: 44%;
	--light-lightness: 94%;
	--white-lightness: 100%;
	--color-primary: hsl(var(--primary-hue), 89%, 41%);
	--color-white: hsl(0, 5%, var(--white-lightness));
	--color-light: hsl(var(--primary-hue), 5%, var(--light-lightness));
	--color-black: hsl(var(--primary-hue), 5%, var(--black-lightness));
	--color-dark: hsl(var(--primary-hue), 5%, var(--dark-lightness));
	--color-success: green;
	--color-failure: red;

	/* General container variables */
	--container-width-lg: 76%;
	--container-width-md: 90%;

	/* BORDER RADIUS VARIABLES */
	--border-radius-1: 1.2rem;
	--border-radius-2: 0.8rem;
	--border-radius-3: 0.5rem;

	/* GENERAL TRANSITION VARIABLE */
	--transition: all 500ms ease;
}

html {
	scroll-behavior: smooth;
}

::-webkit-scrollbar {
	display: none;
}

body {
	font-family: "Montserrat", sans-serif;
	line-height: 1.6;
	font-size: 0.9rem;
	background-color: var(--color-black);
	color: var(--color-light);
}

/* THEME change using react */
main.color-1 {
	--primary-hue: 270;
}
main.color-2 {
	--primary-hue: 110;
}
main.color-3 {
	--primary-hue: 257;
}
main.color-4 {
	--primary-hue: 325;
}
main.color-5 {
	--primary-hue: 0;
}
main.color-6 {
	--primary-hue: 55;
}

main {
	color: var(--color-dark);
	--color-primary: hsl(var(--primary-hue), 89%, 41%);
}

main.bg-2 {
	--black-lightness: 96%;
	--dark-lightness: 60%;
	--light-lightness: 12%;
	--white-lightness: 6%;
	--color-white: hsl(0, 5%, var(--white-lightness));
	--color-light: hsl(var(--primary-hue), 5%, var(--light-lightness));
	--color-black: hsl(var(--primary-hue), 5%, var(--black-lightness));
	--color-dark: hsl(var(--primary-hue), 5%, var(--dark-lightness));
}

/* main {
    color: var(--color-dark);
    --color-primary: hsl(var(--primary-hue), 89%, 41%);
} */

/* GENERAL CONTAINER CLASS */
.container {
	width: var(--container-width-lg);
	margin-inline: auto;
	max-width: 1920px;
}

/* SECTION */
section {
	padding: 10rem 0;
	background: var(--color-light);
	transition: var(--transition);
}

section:nth-child(even) {
	background: var(--color-white);
}

section > h2 {
	text-align: center;
}

section > p {
	text-align: center;
	margin: 0.5rem auto 4rem;
	width: 40%;
}

/* GENERAL SECTION STYLES */
a {
	/* color: var(--color-white); */
	transition: var(--transition);
}

/* GENERAL HEADING STYLES */

h1,
h2,
h3,
h4,
h5,
h6,
hr {
	line-height: 1.1;
	color: var(--color-white);
}

h1 {
	font-size: 3rem;
}

h2 {
	font-size: 2rem;
}

h3 {
	font-size: 1.6rem;
}

h4 {
	font-size: 1.1rem;
}

h5 {
	font-size: 0.95rem;
}

h6 {
	font-size: 0.8rem;
}

/* GENERAL IMAGES STYLES */
img {
	display: block;
	width: 100%;
	object-fit: cover;
}

/* GENERAL BUTTON STYLES */
.btn {
	display: inline-block;
	width: fit-content;
	display: flex;
	align-items: center;
	gap: 1rem;
	background: transparent;
	color: var(--color-light);
	padding: 0.6rem 2.5rem;
	border: 2px solid transparent;
	border-radius: var(--border-radius-2);
	cursor: pointer;
}

.btn:hover {
	background-color: transparent;
	border-color: var(--color-light);
	/* transform: translateY(-0.5rem); */
}

.btn.white {
	background: var(--color-white);
}

.btn.white:hover {
	background: transparent;
	border-color: var(--color-white);
}

.btn.success {
	background: transparent;
	color: var(--color-success);
	/* text color always is always white */
}

.btn.success:hover {
	background: var(--color-success);
	color: aliceblue;
	/* text color always is always white */
}

.btn.disabled {
	opacity: 0.6; /* Reduce opacity to indicate disabled state */
	cursor: not-allowed; /* Show not-allowed cursor when hovering */
	pointer-events: none; /* Disable pointer events to prevent interaction */
	/* Add any additional styling as desired */
}

.btn.primary {
	background: var(--color-primary);
	color: aliceblue;
	/* text color always is always white */
}

.btn.primary:hover {
	background: transparent;
	border-color: var(--color-primary);
	color: var(--color-primary);
}

.btn.sm {
	padding: 0.5rem 1.2rem;
}

/* TABLETS */
@media screen and (max-width: 1024px) {
	body {
		font-size: 1rem;
	}

	.container {
		width: var(--container-width-md);
	}

	section {
		padding: 8rem 0;
	}

	section > p {
		width: 72%;
	}

	h1 {
		font-size: 2rem;
	}
}

/* PHONES */
@media screen and (max-width: 600px) {
	h1 {
		font-size: 2.4rem;
	}

	h2 {
		font-size: 1.8rem;
	}

	h3 {
		font-size: 1.4rem;
	}

	h4 {
		font-size: 1rem;
	}

	.btn {
		padding: 0.6rem 1.5rem;
	}

	section > p {
		width: var(--container-width-md);
	}
}
